import React from 'react';
import {
  TabBarBox,
  TabBarItem,
  TabBarInner,
  ItemIcon,
  ItemTitle,
} from './styles';
import iconLibrary from './images/icon-library.svg';
import iconFind from './images/icon-find.svg';
import iconMy from './images/icon-my.svg';

interface TabBarProps {
  handelClick: (type: string) => void;
  type: string;
}

const TabBar: React.FC<TabBarProps> = (props) => {
  const { handelClick, type } = props;
  return (
    <TabBarBox>
      <TabBarInner>
        <TabBarItem
          onClick={(): void => handelClick('library')}
          className={type === 'library' ? 'active' : ''}
        >
          <ItemIcon src={iconLibrary} />
          <ItemTitle>题库</ItemTitle>
        </TabBarItem>
        <TabBarItem
          onClick={(): void => handelClick('find')}
          className={type === 'find' ? 'active' : ''}
        >
          <ItemIcon src={iconFind} />
          <ItemTitle>发现</ItemTitle>
        </TabBarItem>
        <TabBarItem
          onClick={(): void => handelClick('user-center')}
          className={type === 'user-center' ? 'active' : ''}
        >
          <ItemIcon src={iconMy} />
          <ItemTitle>我的</ItemTitle>
        </TabBarItem>
      </TabBarInner>
    </TabBarBox>
  );
};

export default TabBar;
